export default {
  name: "WhitelistCard",
  props: {
    showWhitelist: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      MintNum: 0,
      visables: this.showWhitelist,
    }
  },
   watch: {
    showWhitelist(n) {
      this.visables = n;
    },
  },
  methods: {
    close() {
      this.$emit("closeWhitelistcard", false);
    }
  }
}

